.proyecto-uno {
  display: flex;
  width: 40%;
  align-items: center;
  justify-content: center;
  background-color: #000;
  position: relative;
  overflow: hidden;
  border: 2px solid #000;
  margin: 0.5rem 0;
  max-height: 200px;
}

.proyecto-uno img {
  width: 100%;
  transition: all 0.5s;
}

.transparente {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #fff;
  transition: all 0.5s;
  background-color: #000;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.nombre-del-proyecto-uno {
  font-size: 0.8rem;
}

.descripcion-proyecto-uno {
  text-align: center;
  font-size: 0.5rem;
}

.proyecto-link {
  text-align: center;
  font-size: 0.7rem;
}

.proyecto-uno:hover .transparente,
.proyecto-uno:active .transparente {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.proyecto-uno:hover img,
.proyecto-uno:active img {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

