@media (max-width: 1275px) and (min-width: 801px) {
  .contenedor-proyectos-titulo {
    position: relative;
    top: 430px;
  }

  .titulo {
    margin-top: 20px;
  }

  .proyecto-uno {
    width: 100%;
  }

  .proyecto-dos {
    width: 100%;
  }

  .proyecto-tres {
    width: 100%;
  }
}
